body {
  font-family: 'Segoe UI', 'Roboto', 'Arial', sans-serif;
  background: #f5f7fa;
  color: #222;
}

.chatbot-container {
  width: 400px;
  height: 500px;
  border: 1px solid #ccc;
  border-radius: 8px;
  display: flex;
  flex-direction: column;
  position: fixed;
  bottom: 20px;
  right: 20px;
  background-color: #fff;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
  overflow: hidden;
}

.chatbot-header {
  background-color: #007bff;
  color: white;
  padding: 10px;
  text-align: center;
}

.chatbot-messages {
  flex: 1;
  padding: 10px;
  overflow-y: auto;
}

.message {
  margin: 5px 0;
  padding: 8px;
  border-radius: 5px;
}

.message-user {
  background-color: #e1ffc7;
  align-self: flex-end;
}

.message-bot {
  background-color: #f1f1f1;
  align-self: flex-start;
}

.chatbot-input {
  display: flex;
  padding: 10px;
}

.chatbot-input input {
  flex: 1;
  padding: 10px;
  border: 1px solid #ccc;
  border-radius: 5px;
}

.chatbot-input button {
  padding: 10px;
  margin-left: 5px;
  border: none;
  border-radius: 5px;
  background-color: #007bff;
  color: white;
  cursor: pointer;
}

.chatbot-input button:hover {
  background-color: #0056b3;
}

.product-card {
  background: #fff;
  border-radius: 18px;
  box-shadow: 0 4px 24px rgba(0, 0, 0, 0.08);
  padding: 24px 18px 18px 18px;
  display: flex;
  flex-direction: column;
  align-items: center;
  transition: box-shadow 0.2s;
  border: none;
}

.product-card:hover {
  box-shadow: 0 8px 32px rgba(0, 0, 0, 0.12);
}

.product-card img {
  width: 140px;
  height: 140px;
  object-fit: cover;
  border-radius: 16px;
  margin-bottom: 16px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.07);
}

.product-card h3 {
  font-size: 1.2rem;
  font-weight: 700;
  margin: 10px 0 4px 0;
  color: #222;
}

.product-card p {
  color: #007bff;
  font-size: 1.1rem;
  font-weight: 600;
  margin: 0 0 10px 0;
}

.product-card button {
  background: linear-gradient(90deg, #007bff 60%, #00c6ff 100%);
  color: #fff;
  border: none;
  border-radius: 24px;
  padding: 10px 28px;
  font-size: 1rem;
  font-weight: 700;
  margin-top: 10px;
  box-shadow: 0 2px 8px rgba(0, 123, 255, 0.08);
  cursor: pointer;
  transition: background 0.2s;
}

.product-card button:hover {
  background: linear-gradient(90deg, #0056b3 60%, #007bff 100%);
}

.shopping-cart-container {
  max-width: 440px;
  margin: 2.5rem auto;
  background: #fff;
  border-radius: 18px;
  box-shadow: 0 4px 24px rgba(0, 0, 0, 0.08);
  padding: 32px 24px 24px 24px;
}

.shopping-cart-container h2 {
  font-size: 1.5rem;
  font-weight: 700;
  color: #222;
  margin-bottom: 18px;
}

.shopping-cart-container ul {
  padding: 0;
  list-style: none;
}

.shopping-cart-container li {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 16px;
  padding-bottom: 10px;
  border-bottom: 1px solid #f0f0f0;
}

.shopping-cart-container strong {
  color: #007bff;
}

.shopping-cart-container button {
  background: linear-gradient(90deg, #ff416c 60%, #ff4b2b 100%);
  color: #fff;
  border: none;
  border-radius: 24px;
  padding: 8px 22px;
  font-size: 1rem;
  font-weight: 700;
  margin: 8px 8px 0 0;
  box-shadow: 0 2px 8px rgba(255, 65, 108, 0.08);
  cursor: pointer;
  transition: background 0.2s;
}

.shopping-cart-container button:hover {
  background: linear-gradient(90deg, #ff4b2b 60%, #ff416c 100%);
}

.shopping-cart-container input[type='number'] {
  border: 1px solid #ddd;
  border-radius: 8px;
  width: 48px;
  text-align: center;
  padding: 4px 0;
  margin: 0 6px;
}

@media (max-width: 600px) {
  .shopping-cart-container,
  .product-card {
    padding: 12px 6px;
    max-width: 98vw;
  }

  .product-card img {
    width: 90px;
    height: 90px;
  }
}